
$color-brand: #C82E24;
$color-brand-hover: #99221D;
$color-background: #FFF;
$color-lightBackground: #FAFAFA;
$color-darkBackground: #EDEDED;
$color-darkGrayBackground: #D8D8D8;
$color-text: #000;
$color-text-light: #FFF;
$color-text-gray: #6D7278;
$color-divider: rgba(0, 0, 0, 0.3);

@function rem($px, $base: 16) { @return ($px / $base) * 1rem; }

$fs-primary-h1:   rem(32);
$fs-secondary-h1: rem(24);
$fs-menu:         rem(22);
$fs-button:       rem(22);
$fs-h2:           rem(20);
$fs-regular:      rem(16);
$fs-p:            rem(14);

$fs-primary-h1-xl:   rem(28);
$fs-secondary-h1-xl: rem(20);
$fs-menu-xl:         rem(18);
$fs-button-xl:       rem(18);
$fs-h2-xl:           rem(18);
$fs-regular-xl:      rem(14);
$fs-p-xl:            rem(14);

$fs-primary-h1-lg:   rem(20);
$fs-secondary-h1-lg: rem(16);
$fs-menu-lg:         rem(14);
$fs-button-lg:       rem(14);
$fs-h2-lg:           rem(14);
$fs-regular-lg:      rem(12);
$fs-p-lg:            rem(12);

$fw-light:   300;
$fw-regular: 400;
$fw-semibold: 600;
$fw-bold:    700;

//    
$font-h1:         $fw-bold   #{$fs-secondary-h1}/1.2 "Open Sans", Arial, sans-serif;
$font-h2:         $fw-semibold #{$fs-h2}/1.3 "Open Sans", Arial, sans-serif;
$font-menu:       $fw-regular  #{$fs-menu}/1.2 "Open Sans", Arial, sans-serif;
$font-p:          $fw-regular  #{$fs-p}/1.6 "Open Sans", Arial, sans-serif;
$font-button:     $fw-semibold #{$fs-button}/1.2 "Open Sans", Arial, sans-serif;
$font-regular:    $fw-regular  #{$fs-regular}/1.2 "Open Sans", Arial, sans-serif;

$transition-duration: 0.2s;