@use "_fonts" as *;
@use "_variables" as *;
@use "../../node_modules/bootstrap/scss/bootstrap";

.footer {
  padding: 1.5rem 0;
  background-color: $color-darkBackground;
  @include bootstrap.media-breakpoint-down(md) {
    padding: 0.75rem 0;
    text-align: center;
  }
}

.footer__contacts {
  font: $font-regular;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
  @include bootstrap.media-breakpoint-down(md) {
    margin-bottom: 0.5rem;
  }
  p {
    padding: 0;
    margin: 0 0 0.5rem;
  }
  div {
    margin: 0 0 0.5rem;
    &:last-child {
      margin: 0; //   
    }
    a{
      color: $color-text;
      text-decoration: none;
      transition: color $transition-duration ease;
      &:hover {
        color: $color-brand;
      }
    }
  }
}

.footer__logo {
  color: $color-brand;
  @include bootstrap.media-breakpoint-down(md) {
    margin-bottom: 0.5rem;
  }
  h3 {
    margin: 0;
    font-size: 3rem;
    font-weight: $fw-bold;
    text-shadow: 0 1px 1px rgba(0,0,0,.8);
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: 2.5rem;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: 2rem;
    }
  }
  p {
    margin: 0;
    text-transform: uppercase;
    font-size: rem(18);
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: rem(15);
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: rem(10);
    }
  }
}

.footer__social_media {
  a {
    width: rem(50);
    height: rem(50);
    margin-right: 1.25rem;
    cursor: pointer;
    transition: transform $transition-duration ease;
    &:hover {
      transform: translateY(-0.25rem);
    }
    &:last-child {
      margin-right: 0;
    }
    @include bootstrap.media-breakpoint-down(xl) {
      width: rem(40);
      height: rem(40);
    }
    @include bootstrap.media-breakpoint-down(lg) {
      width: rem(32);
      height: rem(32);
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
}