@use "_fonts" as *;
@use "_variables" as *;
@use "../../node_modules/bootstrap/scss/bootstrap";

.product {
  h1 {
    font: $font-h1;
    font-weight: $fw-regular;
    font-size: $fs-primary-h1;
    padding-bottom: 0.5rem;
    color: $color-brand;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-primary-h1-xl;
      padding-bottom: 0.25rem;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-primary-h1-lg;
      padding-bottom: 0.25rem;
    }
    @include bootstrap.media-breakpoint-down(md) {
      text-align: center;
    }
  }
}
.product-gallery {
  padding: 0.5rem 0 0.5rem 1rem;
  @include bootstrap.media-breakpoint-down(lg) {
    padding: 0.5rem 0 0.5rem 0;
  }
}

.product-document {
  padding: 0.5rem 1rem;
  a {
    justify-content: left;
    align-items: center;
    text-decoration: none;
    font: $font-p;
    font-size: $fs-regular;
    color: $color-text;
    transition: color $transition-duration ease;
    cursor: pointer;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-regular-xl;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-regular-lg;
    }
    &:hover {
      color: $color-brand;
    }
    img {
      height: rem(24);
      width: auto;
      margin-right: 1rem;
      @include bootstrap.media-breakpoint-down(xl) {
        height: rem(20);
      }
    }
    p {
      margin: 0;
    }
  }
}

.product-info__container {
  padding: 0.5rem 0;
}

.product-info {
  padding: 0.5rem 1rem;
  background-color: $color-lightBackground;
  border-radius: 0.25rem;
  height: 100%;
  overflow: scroll;
}

.info-item {
  margin-bottom: 0.75rem;
}

.info-item__name {
  font: $font-p;
  font-size: $fs-p;
  color: $color-text-gray;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-p-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-p-lg;
  }
}

.info-item__value {
  font: $font-p;
  font-size: $fs-regular;
  color: $color-text;
  line-height: 1.2rem;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
    line-height: 0.8rem;
  }
}

.info-item__header {
  font: $fs-secondary-h1;
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-secondary-h1-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-secondary-h1-lg;
  }
}

.catalog-h2 {
  padding: 2rem 0 1rem 0;
  text-align: center;
  font-size: $fs-primary-h1;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-primary-h1-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-primary-h1-lg;
  }
}