@use "_fonts" as *;
@use "_variables" as *;
@use "../../node_modules/bootstrap/scss/bootstrap";

.product-item {
  background-color: $color-lightBackground;
  margin: 0.5rem;
  text-decoration: none;
  color: $color-text;
  font-size: $fs-regular;
  cursor: pointer;
  transition: $transition-duration ease;
  border-radius: 6px;
  border: 2px solid $color-lightBackground;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
  &:hover {
    color: $color-brand;
    border-color: $color-brand;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  p {
    padding: 1rem;
    text-align: center;
  }
}