//   
@function font-path($rel) {
  @return "../fonts/Open_Sans/#{$rel}";
}

// Regular
@font-face {
  font-family: "Open Sans";
  src: url(font-path("OpenSans-VariableFont_wdth,wght.ttf")) format("truetype-variations");
  font-style: normal;
  font-weight: 100 900;      /*  wght */
  font-stretch: 75% 125%;    /*  wdth ( ) */
  font-display: swap;
}

/* 2) Variable font  italic */
@font-face {
  font-family: "Open Sans";
  src: url(font-path("OpenSans-Italic-VariableFont_wdth,wght.ttf")) format("truetype-variations");
  font-style: italic;
  font-weight: 100 900;
  font-stretch: 75% 125%;
  font-display: swap;
}