@use "_fonts" as *;
@use "_variables" as *;
@use "../../node_modules/bootstrap/scss/bootstrap";

.tca-card {            // ,     
  background: transparent;
}

.tca-table {
  background: $color-lightBackground;
  border-radius: 0.25rem;
  overflow: hidden;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }

  &__head {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0,1fr));
    background: $color-darkGrayBackground;
    color: $color-text;
    border-radius: 0.25rem;
    font-size: $fs-regular;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-regular-xl;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-regular-lg;
    }
  }
  &__th {
    padding: .75rem 1rem;
    text-align: center;
    &:first-child {
      text-align: left;
    }
    &:last-child {
      text-align: right;
    }
  }

  &__body {
    display: block;
  }
  &__tr {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0,1fr));
    background: $color-lightBackground;
    border-bottom: 1px solid $color-divider;
    color: $color-text;
    transition: $transition-duration ease;
    text-decoration: none;
    cursor: pointer;
    &:last-child {
      border-bottom: none;
    }
    &:hover {
      color: $color-brand;
      border-color: $color-brand;
      background-color: $color-darkBackground;
    }
  }

  &__td {
    padding: .875rem 1rem;
    text-align: center;
    &:first-child {
      text-align: left;
    }
    &:last-child {
      text-align: right;
    }
  }
}

/*  */
.tca-pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem .5rem;
  font-size: $fs-regular;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
  &__prev, &__next {
    color: $color-brand;
    text-decoration: none;
    transition: $transition-duration ease;
    cursor: pointer;
    &:hover {
      color: $color-brand-hover;
    }
    &.is-disabled {
      pointer-events: none;
      opacity: .4;
    }
  }

  &__numbers {
    display: flex;
    gap: .25rem;
    align-items: center;
    font-size: $fs-regular;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-regular-xl;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-regular-lg;
    }
  }
  &__num {
    color: $color-text;
    text-decoration: none;
    padding: 0 .25rem;
    transition: $transition-duration ease;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-regular-xl;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-regular-lg;
    }
    &.is-current {
      color: $color-brand;
      font-weight: 700;
    }
    &:hover {
      color: $color-brand-hover;
    }
  }
  &__gap {
    opacity: .6;
    padding: 0 .25rem;
  }
}

/* :  ,     cols */
@media (max-width: 768px) {
  .tca-table__th, .tca-table__td { padding: .625rem .75rem; }
}