@use "_fonts" as *;
@use "_variables" as *;
@use "../../node_modules/bootstrap/scss/bootstrap";

.section-hero {
  position: relative;

  .swiper {
    width: 100%;
    aspect-ratio: 20 / 9;
    height: auto; //  
    @include bootstrap.media-breakpoint-down(md) {
      min-height: 200px;
    }
  }

  .swiper-slide {
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}

.swiper-pagination-bullet {
  background: #FFFFFF;
  opacity: 0.5;
}

.swiper-pagination-bullet-active {
  background: $color-brand;
  opacity: 1;
}

.swiper-button-prev,
.swiper-button-next {
  width: 44px;
  height: 44px;
  border-radius: 999px;
  @include bootstrap.media-breakpoint-down(md) {
    width: 30px;
    height: 30px;
  }
  //background: rgba(0,0,0,.35);
  //backdrop-filter: blur(2px);
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.swiper-button-prev {
  background-image: url("../images/arrow-left.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  transition: background-color $transition-duration ease;
}
.swiper-button-next {
  background-image: url("../images/arrow-right.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  transition: background-color $transition-duration ease;
}

.swiper-button-prev:hover,
.swiper-button-next:hover,
.swiper-button-prev:focus-visible,
.swiper-button-next:focus-visible {
  background-color: rgba(255,255,255,.35);
}

.hero-image-overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  background: rgba(0,0,0,.35);
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  text-transform: uppercase;

  color: #fff;
  text-shadow: 0 2px 8px rgba(0,0,0,.8);

  h2 {
    font: $font-h1;
    letter-spacing: .03em;
    font-size: rem(128);
    margin: 0;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: rem(96);
    }
    @include bootstrap.media-breakpoint-down(md) {
      font-size: rem(40);
    }
  }
  p  {
    font: $font-p;
    font-size: rem(30);
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: rem(26);
    }
    @include bootstrap.media-breakpoint-down(md) {
      font-size: rem(16);
    }
  }
}

.hero-overlay {
  position: absolute;
  inset: 0;
  display: block;
  place-items: center;
  text-align: center;
}