@use "_fonts" as *;
@use "_variables" as *;
@use "../../node_modules/bootstrap/scss/bootstrap";

.contacts {
  h1 {
    font: $font-h1;
    font-weight: $fw-regular;
    font-size: $fs-primary-h1;
    padding-bottom: 0.5rem;
    color: $color-brand;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-primary-h1-xl;
      padding-bottom: 0.25rem;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-primary-h1-lg;
      padding-bottom: 0;
    }
    @include bootstrap.media-breakpoint-down(md) {
      text-align: center;
    }
  }
}

.contacts__dealers-container {
  background-color: $color-lightBackground;
  border-radius: 0.25rem;
  padding: 0.75rem;
  display: flex;
  justify-content: left;
  flex-flow: wrap;
}

.contacts__dealers-item {
  font: $font-regular;
  color: $color-text;
  text-decoration: none;
  transition: color $transition-duration ease;
  margin: 0.25rem 0.5rem;
  cursor: pointer;
  &:hover {
    color: $color-brand;
  }
  &.active {
    color: $color-brand;
    text-decoration: underline;
    //font-weight: $fw-bold;
    &:hover {
      color: $color-brand-hover;
    }
  }
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
}

.product-info__dealer {
  height: rem(360);
  @include bootstrap.media-breakpoint-down(md) {
    height: auto;
  }
}