@use "_fonts";
@use "../../node_modules/bootstrap/scss/bootstrap" with (
  $font-family-sans-serif: ("Open Sans", Arial, sans-serif)
);
@use "../../node_modules/swiper/swiper";
@use "../../node_modules/swiper/modules/navigation";
@use "../../node_modules/swiper/modules/pagination";
@use "../../node_modules/swiper/modules/effect-fade";

@use "_variables" as *;
@use '_slider';
@use 'productItem';
@use 'product';
@use 'breadcrumbs';
@use "footer";
@use "tableList";
@use "contacts";
@use "burger";
@use "photoswipe"; //   npm   ,   

//  
html, body {
  height: 100%;           //    
  margin: 0;
  background-color: #FFFFFF;
}

body {
  display: flex;
  flex-direction: column; //  body -
}

main {
  flex: 1;                 //    
}

ul {
  margin: 0;         //   
  padding: 0;        //   
  list-style: none;  //  /
}

h2 {
  margin: 0;
  padding: 0;
}

p {
  font-size: $fs-regular;
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
}


.text-justify {
  text-align: justify;
}

// 
.primary-link {
  font: $font-regular;
  text-decoration: none;
  font-size: $fs-regular;
  font-weight: $fw-bold;
  color: $color-text;
  transition: color $transition-duration ease;
  &:hover {
    color: $color-brand;
  }

  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
  @include bootstrap.media-breakpoint-down(md) {
    font-size: $fs-regular;
  }
}

.secondary-link {
  font: $font-regular;
  text-decoration: none;
  font-size: $fs-regular;
  font-weight: $fw-bold;
  color: $color-brand;
  transition: color $transition-duration ease;
  &:hover {
    color: $color-brand-hover;
  }

  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
  @include bootstrap.media-breakpoint-down(md) {
    font-size: $fs-regular;
  }
}

.language-switcher {
  display: inline-block;
  padding: 0;              //    
  border: none;            //   
  background: none;        //   
  width: rem(30);
  height: rem(20);
  border-radius: rem(2);   //  
  overflow: hidden;        //      

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;     //    
    display: block;        //     img
  }

  @include bootstrap.media-breakpoint-down(xl) {
    width: rem(25);
    height: rem(16);
  }

  @include bootstrap.media-breakpoint-down(lg) {
    width: rem(25);
    height: rem(16);
  }
}

.nav-bar1__mobile-container {
  padding: 0.5rem;
}

.nav-bar1__mobile-info-container {
  > * {
    margin-right: 0.5rem;
  }
}

.nav-bar1__burger-container {
  margin-left: auto;
}

.nav-bar1__temples-container {
  a {
    margin-right: 0.5rem;
    @include bootstrap.media-breakpoint-down(lg) {
      margin-right: 0.25rem;
      display: inline-block;
      max-width: 130px;     //   
      white-space: nowrap;  //   
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

.nav-bar1__partners-container {
  a {
    margin-right: 0.5rem;
    @include bootstrap.media-breakpoint-down(lg) {
      margin-right: 0.25rem;
      display: inline-block;
      max-width: 100px;     //   
      white-space: nowrap;  //   
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

.navbar2__container {
  @include bootstrap.media-breakpoint-down(md) {
    border-bottom: 1px solid $color-divider;
  }
}

.navbar2 {
  h2 {
    font-size: 1.75rem;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: 1.5rem;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: 1.15rem;
    }
    @include bootstrap.media-breakpoint-down(md) {
      font-size: 0.9rem;
      line-height: 1rem;
      text-align: center;
      width: fit-content;
    }
  }
}
.navbar2-logo {
  display: block;
  width: rem(132);
  height: rem(132);
  @include bootstrap.media-breakpoint-down(xl) {
    width: rem(100);
    height: rem(100);
  }
  @include bootstrap.media-breakpoint-down(lg) {
    width: rem(70);
    height: rem(70);
  }
  @include bootstrap.media-breakpoint-down(md) {
    width: rem(50);
    height: rem(50);
  }
}

.navbar3 {
  padding: 1rem 0;
  border-top: 1px solid $color-divider;
  border-bottom: 1px solid $color-divider;
  background-color: $color-lightBackground;

  li {
    font: $font-menu;
    margin-right: 2rem;
    cursor: pointer;

    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-menu-xl;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-menu-lg;
    }

    a {
      display: block;
      padding: rem(8) rem(20);
      border-radius: 2px;
      color: $color-text;
      text-decoration: none;
      border: 2px solid transparent;
      transition: border-color $transition-duration ease;
      @include bootstrap.media-breakpoint-down(xl) {
        padding: rem(6) rem(15);
      }
      @include bootstrap.media-breakpoint-down(lg) {
        padding: rem(4) rem(10);
      }
      &:hover{
        border-color: $color-brand;
      }
      &.active {
        background-color: $color-brand;
        color: $color-text-light;
        transition: background-color $transition-duration ease;
        &:hover{
          background-color: $color-brand-hover;
          border-color: $color-brand-hover;
        }
      }
    }
    &:last-child {
      margin-right: 0; //   
    }
  }
}

.section_text-content {
  padding: 1rem 1rem;
  @include bootstrap.media-breakpoint-down(md) {
    padding: 0.25rem 0.5rem;
  }
  h1 {
    font: $font-h1;
    font-weight: $fw-regular;
    font-size: $fs-secondary-h1;
    padding-bottom: 1.25rem;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-secondary-h1-xl;
      padding-bottom: 0.75rem;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-secondary-h1-lg;
      padding-bottom: 0.3rem;
    }
  }

  h2 {
    font: $font-h2;
    font-weight: $fw-regular;
    font-size: $fs-h2;
    padding-bottom: 1.25rem;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-h2-xl;
      padding-bottom: 0.75rem;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-h2-lg;
      padding-bottom: 0.25rem;
    }
    @include bootstrap.media-breakpoint-down(md) {
      padding-top: 0.25rem;
    }
  }

  p {
    font: $font-p;
    @include bootstrap.media-breakpoint-down(xl) {
      font-size: $fs-p-xl;
    }
    @include bootstrap.media-breakpoint-down(lg) {
      font-size: $fs-p-lg;
    }
  }
}

.light-background {
  padding-top: 1rem;
  background-color: $color-lightBackground;
  @include bootstrap.media-breakpoint-down(md) {
    padding-top: 0.25rem;
  }
}

.button-link {
  display: block;
  width: fit-content;
  margin: 0 auto 1rem auto;
  border-radius: 2px;
  font: $font-regular;
  text-decoration: none;
  font-size: $fs-button;
  font-weight: $fw-regular;
  color: $color-text-light;
  transition: background-color $transition-duration ease;
  background-color: $color-brand;
  cursor: pointer;
  padding: 0.5rem 1rem;
  &:hover {
    background-color: $color-brand-hover;
  }
  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-button-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-button-lg;
  }
  @include bootstrap.media-breakpoint-down(md) {
    margin-bottom: 0.25rem;
  }
}

.section_image-content {
  padding: 1rem 0 0;
  img {
    width: 100%;
    height: auto;
  }
  @include bootstrap.media-breakpoint-down(md) {
    padding-top: 0.25rem;
  }
}

.navbar4 {
  font: $font-regular;
  padding: 1.125rem 0;
  background-color: $color-darkBackground;
  border-bottom: 1px solid $color-divider;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;

  @include bootstrap.media-breakpoint-down(xl) {
    font-size: $fs-regular-xl;
  }
  @include bootstrap.media-breakpoint-down(lg) {
    font-size: $fs-regular-lg;
  }
  .container nav ul {
    min-width: max-content;
    li {
      a {
        color: $color-text;
        text-decoration: none;
        transition: color $transition-duration ease;
        margin-right: 1rem;
        &.active {
          color: $color-brand;
        }
        &:hover {
          color: $color-brand;
        }
      }
    }
  }
}

.section-gallery {
  border-radius: 0.25rem;
  overflow: hidden;
  .swiper {
    width: 100%;
  }
  .swiper-slide img {
    width: 100%;
    height: auto;
    display: block;
  }
}