@use "_variables" as *;

.offcanvas-custom {
  width: 300px; //  320360

  .offcanvas-header { border-bottom: 1px solid $color-divider; }
  .offcanvas-footer { border-top: 1px solid $color-divider; padding: .75rem 1rem; }

  .offcanvas-topbar {
    display: flex; align-items: center; justify-content: space-between;
    padding: .75rem 1rem; border-bottom: 1px solid $color-divider;

    .offcanvas-phone {
      font-weight: $fw-bold; color: $color-text; text-decoration: none;
      &:hover { color: $color-brand; }
    }
    .offcanvas-lang {
      list-style: none; margin: 0; padding: 0; display: flex; gap: .5rem;
    }
  }

  .offcanvas-nav {
    padding: .5rem 0;

    .menu-root,
    .menu-sub {
      list-style: none; margin: 0; padding: 0;
    }

    .menu-link,
    .menu-sublink,
    button.menu-link {
      display: block;
      width: 100%;
      text-align: left;
      padding: .625rem 1rem;
      text-decoration: none;
      color: $color-text;
      background: transparent;
      border: 0;
      cursor: pointer;
      transition: color $transition-duration ease, background-color $transition-duration ease;

      &:hover { color: $color-brand; background-color: $color-darkBackground; }
    }

    // - (  )   
    button.menu-link {
      position: relative;
      padding-right: 2rem;

      &::after {
        content: "";
        position: absolute; right: 1rem; top: 50%; transform: translateY(-50%) rotate(0deg);
        width: .6rem; height: .6rem; border-right: 2px solid currentColor; border-bottom: 2px solid currentColor;
        transform-origin: center; transition: transform $transition-duration ease;
        /* - */
        transform: translateY(-50%) rotate(45deg);
      }

      &[aria-expanded="true"]::after {
        transform: translateY(-50%) rotate(-135deg); /*  */
      }
    }

    .menu-sub {
      padding: .25rem 0 .5rem;
      .menu-sublink {
        padding-left: 2rem;
        font-size: $fs-regular;
      }
    }
  }
}

/* -   */
.navbar-toggler {
  width: 40px; height: 40px; display: inline-flex; align-items: center; justify-content: center;
  img { display: block; width: 28px; height: 28px; }
}

/*  (    .language-switcher    ) */
.language-switcher {
  display: inline-block; width: 30px; height: 20px; padding: 0; border: 0; background: none; border-radius: 2px; overflow: hidden;
  img { width: 100%; height: 100%; object-fit: cover; display: block; }
}